.house-header {
  height:340px;
  background-size:cover;
  position:relative;

  .header-info { 
    position:absolute;
    left:30px;
    bottom:30px;

    h1.ant-typography, h2.ant-typography, span.ant-typography {
      margin:4px 0 0 0;
      color:#fff;
      text-shadow:0 0 6px rgba(0,0,0,.9);
    }

    h2.ant-typography {
      font-weight:normal;
    }

    span.ant-typography {
      font-size:22px;
    }
  }
}